
:root {
  font-size: 16px;
}

@media (max-width: 700px) {
  .card-columns.custom-columns {
    column-count: 1;
  }
}
  
@media (min-width: 700px) {
  .card-columns.custom-columns {
    column-count: 2; 
  }
}

@media (min-width: 1000px) {
  .card-columns.custom-columns {
    column-count: 3;}
  }
@media (min-width: 1400px) {
  .card-columns.custom-columns {
    column-count: 4;}
  }

.clock-box {
 display: flex;
 justify-content:flex-end;
}

.clock {
  padding: 0.2rem 2rem;
  margin: 0.2rem 0.6rem;
  margin-bottom: 0;
  padding-bottom: 0;
  font-weight: 500;
  font-size: 1.3rem;
}

.notilink {
  text-decoration: none;
  color: #212529;
}
.notilink:hover {
  color: #212529;
  text-decoration: none;
}
